section.gallery-1 {
    padding: 0;
    @include tablet {
        padding: 75px; }
    @include desktop {
        padding: 100px; }
    h2 {
        text-align: center;
        font-size: 3em; }
    div.description {
        text-align: center;
        max-width: 550px;
        width: 90%;
        margin-left: auto;
        margin-right: auto; }
    .buttonwrapper {
        width: 100%;
        text-align: center; }
    ul.photos {
        list-style-type: none;
        display: grid;
        margin-left: auto;
        margin-right: auto;
        padding-left: 0;
        text-align: center;
        justify-items: center;
        justify-content: center;
        grid-template-columns: repeat(auto-fill, 300px);
        grid-gap: 20px;
        img {
            cursor: pointer; } }
    div.image-viewer {
        display: none;
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 100%;
        width: 100%;
        z-index: 20;
        background: rgba(0,0,0,.9);
        &.show {
            display: block; }
        a.close-viewer {
            position: absolute;
            top: 10px;
            right: 10px;
            color: $white;
            text-decoration: none; }
        a.prev-image {
            position: absolute;
            left: 10px;
            top: 50%;
            color: $white;
            text-decoration: none; }
        a.next-image {
            position: absolute;
            right: 10px;
            top: 50%;
            color: $white;
            text-decoration: none; }
        .image {
            background-color: $gray;
            margin: 0 auto;
            padding: 20px;
            margin-top: 10vh;
            width: 80%;
            position: relative;
            img {
                width: 100%;
                max-height: 75vh;
                border: 5px solid $gray; } } } }


