footer.style-1 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 50px 50px .5em 50px;
    background: lighten($gray, 45%);
    ul {
        list-style-type: none;
        padding-left: 0;
        margin-bottom: 1em;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-wrap: wrap;
        @include tablet {
            margin-bottom: 0; }
        li {
            margin-right: 1em;
            margin-bottom: 0;
            small {
                line-height: 20px; }
            a {
                color: $bright;
                text-decoration: none;
                font-weight: 700;
                svg {
                    width: 20px;
                    height: 20px;
                    fill: $bright; } } } } }
footer.sub {
    padding: 0 50px 10px 50px;
    background: lighten($gray, 45%);
    // text-align: center
    p {
        margin-bottom: 0; } }
footer.style-2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    background: lighten($gray, 45%);
    img.logo {
        margin-left: 2em;
        display: none;
        @include tablet {
            display: block; } }
    ul.right {
        margin-left: 2em;
        @include tablet {
            margin-left: 0; }
        margin-right: 2em; }
    ul.center {
        display: none;
        @include desktop {
            display: block; } }
    ul {
        list-style-type: none;
        padding-left: 0;
        margin-bottom: 2em;
        margin-top: 2em;
        li {
            display: inline-block;
            margin-right: 1em;
            margin-bottom: 0;
            a {
                color: $bright;
                text-decoration: none;
                font-weight: 700;
                svg {
                    width: 1em;
                    height: 1em;
                    fill: $bright; } } } }
    div.bottom {
        width: 100%;
        text-align: center;
        padding: 1em;
        border-top: 1px solid $gray; } }
footer.style-3 {
    background: lighten($gray, 45%);
    div.top {
        display: flex;
        flex-wrap: wrap;
        padding: 2em;
        @include tablet {
            padding: 75px 75px 0px 75px; }
        p {
            margin: 0 1em 2em 0;
            @include tablet {
                margin: 0 4em 2em 0; } }
        span {
            font-size: 1.1em;
            font-weight: 700; }
        p + p {
            text-indent: 0; }
        ul.footer-menu {
            list-style-type: none;
            display: none;
            @include tablet {
                display: block; }
            padding-left: 0;
            font-weight: 700;
            flex-grow: 2;
            text-align: right;
            li {
                line-height: 2; }
            a {
                color: $dark;
                text-decoration: none; } } }
    div.bottom {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        padding: 1em;
        border-top: 1px solid lighten($gray, 50%);
        p {
            margin: 0 1em 0 0; }
        ul {
            list-style-type: none;
            display: flex;
            padding-left: none;
            margin-bottom: 0;
            li {
                margin-right: .5em;
                margin-bottom: 0; } }
        a {
            height: 17px;
            margin: 0 .5em 0 0;
            svg {
                display: none;
                @include tablet {
                    display: block; }
                height: 17px;
                width: inherit; } } } }
footer.style-4 {
    background: lighten($gray, 45%);
    div.top {
        display: flex;
        flex-wrap: wrap;
        padding: 2em;
        @include tablet {
            padding: 75px 75px 0px 75px; }
        p {
            margin: 0 1em 2em 0;
            @include tablet {
                margin: 0 4em 2em 0; } }
        span {
            font-size: 1.1em;
            font-weight: 700; }
        p + p {
            text-indent: 0; }
        div.footer-menu {
            display: none;
            @include tablet {
                display: block; }
            p {
                font-size: 1.1em;
                font-weight: 700;
                margin-bottom: 0; }
            ul {
                list-style-type: none;
                display: none;
                @include tablet {
                    display: block; }
                padding-left: 0;
                font-weight: 400;
                li {
                    line-height: 2; }
                a {
                    color: $dark;
                    text-decoration: none;
                    &:hover {
                        text-decoration: underline; } } } }
        div.social-links {
            display: none;
            @include tablet {
                display: block; }
            p {
                font-size: 1.1em;
                font-weight: 700;
                margin-bottom: 0; }
            ul {
                list-style-type: none;
                padding-left: 0;
                font-weight: 400;
                li {
                    line-height: 2;
                    display: inline-block; }
                a {
                    color: $dark;
                    text-decoration: none;
                    svg {
                        height: 17px;
                        width: inherit; } } } } }
    div.bottom {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        padding: 1em;
        border-top: 1px solid lighten($gray, 50%);
        p {
            margin: 0; }
        a {
            height: 17px;
            svg {
                display: none;
                @include tablet {
                    display: block; }
                height: 17px; } } } }


