// Style the logo and nav trigger for phones.
nav.mobile-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px 35px;
    z-index: 2;
    position: absolute;
    top: 0;
    width: 100%;
    @include tablet {
        display: none; }
    a.trigger {
        padding: 13px 30px;
        background-color: $dark;
        color: white;
        text-decoration: none;
        border-radius: 3px; }
    a.trigger:hover {
        background: transparent;
        color: $dark;
        border: 1px solid $dark;
        padding: 12px 29px; } }

nav.mobile-nav.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 20px;
    height: 60px;
    z-index: 2;
    position: sticky;
    top: 0;
    width: 100%;
    background-color: $dark;
    @include tablet {
        display: none; }
    a.trigger {
        padding: 0;
        background-color: none;
        color: white;
        text-decoration: none;
        border-radius: 0; }
    a.trigger:hover {
        background: transparent;
        color: $dark;
        border: none;
        padding: 0; } }

// Style the menu that shows up on tablets and larger.
#panel nav.navigation-bar-1 {
    display: none;
    z-index: 4;
    position: absolute;
    top: 0;
    width: 100%;
    @include tablet {
        width: 100%;
        display: flex;
        align-items: center; }
    ul li a {
        color: white; }

    div.left {
        padding: 46px 0px 46px 70px;
        flex-grow: 1;
        display: flex;
        align-items: center;
        a.logo {
            margin-right: 32px; }
        ul {
            list-style-type: none;
            padding-left: 0;
            margin-bottom: 0;

            li {
                margin-right: 32px;
                display: inline-block;
                position: relative;

                a {
                    text-decoration: none; } }

            li a:hover {
                color: $gray; }
            li > ul {
                display: none;
                flex-direction: column;
                width: 240px; }
            li:hover > ul {
                position: absolute;
                display: block;
                background: $light-gray;
                padding-top: 5px;
                li {
                    padding: 10px;
                    width: 100%;
                    border-bottom: 1px solid $dark;
                    a {
                        color: $dark; } }
                li:last-of-type {
                    border-bottom: none; }
                li a:hover {
                    color: $gray; } } } }
    div.right {
        display: none;
        @include desktop {
            display: flex; }
        padding: 46px 70px;
        flex-grow: 0;
        align-items: center;
        ul {
            list-style-type: none;
            padding-left: 0;
            margin-bottom: 0;

            li {
                margin-right: 32px;
                display: inline-block;

                a {
                    text-decoration: none; } }
            li a:hover {
                color: lighten($dark, .3); }
            li > ul {
                display: none;
                flex-direction: column;
                width: 240px; }
            li:hover > ul {
                position: absolute;
                display: block;
                background: $light-gray;
                padding-top: 5px;
                li {
                    padding: 10px;
                    width: 100%;
                    border-bottom: 1px solid $dark; }
                li:last-of-type {
                    border-bottom: none; }
                li a {
                    color: $dark; }
                li a:hover {
                    color: $gray; } } }
        a.cta-button {
            padding: 13px 30px;
            background-color: $dark;
            color: white;
            text-decoration: none;
            border-radius: 3px; }
        a.cta-button:hover {
                background: transparent;
                color: $dark;
                border: 1px solid $dark;
                padding: 12px 29px; } } }
