section.features-1 {
    display: grid;
    padding: 100px;
    @include tablet {
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: 40px; }
    @include desktop {
        grid-template-columns: repeat(4, 1fr);
        grid-column-gap: 40px; }
    .feature {
        margin-bottom: 40px; } }
section.features-2 {
    display: grid;
    padding: 100px;
    @include tablet {
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 40px; }
    .feature {
        margin-bottom: 40px;
        p {
            font-size: 1.15em; } } }
section.features-3 {
    display: grid;
    padding: 100px;
    text-align: center;
    justify-items: center;
    @include tablet {
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 40px; }
    @include desktop {
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: 40px; }
    .feature {
        margin-bottom: 40px;
        max-width: 290px; } }
section.features-4 {
    display: grid;
    padding: 100px;
    text-align: center;
    justify-items: center;
    @include tablet {
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 40px; }
    .feature {
        max-width: 290px;
        margin-bottom: 40px;
        &.last-of-type {
            margin-bottom: 0; }
        @include tablet {
            margin-bottom: 0; } } }
section.features-5 {
    display: flex;
    padding: 100px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    h2 {
        margin-bottom: 2em;
        font-size: 2em; }
    .grid {
        display: grid;
        grid-column-gap: 30px;
        justify-items: center;
        @include desktop {
            grid-template-columns: repeat(3, 1fr); }
        .feature {
            border: 1px solid $gray;
            padding: 40px;
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;
            max-width: 360px;
            margin-bottom: 30px; } } }

section.features-6 {
    display: flex;
    padding: 100px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    h2 {
        margin-bottom: 2em;
        font-size: 2em;
        text-align: center; }
    ul {
        @include tablet {
            column-count: 2;
            column-gap: 100px; }
        list-style-type: none;
        li {
            font-size: 1.3em;
            padding-left: 50px;
            padding-top: 5px;
            background: url(../images/icon.png) no-repeat top left;
            min-height: 55px;
            margin-bottom: 1em;
            p {
                font-weight: 200;
                font-size: 1em; } } } }
section.features-7 {
    display: grid;
    @include tablet {
        grid-template-columns: repeat(2, 1fr); }
    .feature {
        padding: 100px;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center; }
    .feature:nth-child(even) {
        border-top: 1px solid $light-gray;
        @include tablet {
            border-top: none;
            border-left: 1px solid $light-gray; } } }
section.features-8 {
    padding: 100px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    h2 {
        font-size: 3em; }
    .features-grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 2em;
        grid-row-gap: 2em;
        @include tablet {
            grid-template-columns: repeat(4, 1fr); }
        .feature {
            max-width: 263px; } } }
section.features-9 {
    padding: 100px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h2 {
        font-size: 3em;
        margin-bottom: 0; }
    p {
        margin-bottom: 2em; }
    .features-grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: 2em;
        grid-row-gap: 2em;
        justify-items: center;
        max-width: 500px;
        margin-bottom: 1.5em;
        .feature {
            max-width: 150px; } } }
section.features-tabs {
    padding: 75px;
    @include desktop {
        padding: 100px; }
    .feature-area {
        display: flex;
        flex-direction: column;
        border-bottom: 2px dashed $gray;
        padding-bottom: 4em;
        @include tablet {
            grid-template-columns: repeat(12, 1fr);
            margin-bottom: 90px;
            display: none;
            border-bottom: none;
            padding-bottom: 0;
            &.active {
                display: grid; } }
        .feature-text {
            grid-column-start: 1;
            grid-column-end: 5;
            align-self: center;
            text-align: center;
            @include tablet {
                text-align: left; }
            h2 {
                font-size: 2em;
                @include tablet {
                    font-size: 3em; }
                margin-bottom: 0; } }
        .feature-image {
            grid-column-start: 7;
            grid-column-end: 13;
            text-align: center;
            justify-self: center;
            align-self: center; } }
    .feature-tabs-tabs {
        grid-template-columns: repeat(4, 1fr);
        border-top: 4px solid #E4F1FD;
        display: none;
        @include tablet {
            display: grid; }
        .tab {
            padding-top: 30px;
            margin-top: -4px;
            &.active {
                border-top: 4px solid #3FA2F7; }
            a {
                color: inherit;
                text-decoration: none; } } } }








