section.signup-1 {
    background: lighten($gray, 50%);
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    div.main {
        padding: 50px;
        justify-self: center;
        align-self: center;
        grid-column: 1 / 12;
        @include tablet {
            padding: 90px;
            grid-column: 1 / 7; }
        @include desktop {
            padding: 175px 149px; }
        form {
            input {
                background: white;
                margin-bottom: 1em;
                border-radius: 0;
                border: none;
                color: $dark; }
            input[type="submit"] {
                background: $bright;
                color: white; } } }
    div.image {
        justify-self: end;
        align-self: center;
        grid-column: 7 / 12;
        img {
            width: 100%; }
        display: none;
        @include tablet {
            display: block; } } }
section.signup-2 {
    text-align: center;
    padding: 80px;
    form {
        display: flex;
        justify-content: center;
        align-items: center;
        input[type="email"] {
            width: 350px;
            height: 3em;
            background: lighten($gray, 50%);
            border: none;
            border-radius: 0;
            color: $dark;
            padding-left: 1em;
            padding-right: 1em; }
        input[type="submit"] {
            height: 3em;
            background: $bright;
            color: white;
            border: none;
            border-radius: 0; } } }
section.signup-3 {
    display: grid;
    @include tablet {
        grid-template-columns: 35% 65%; }
    grid-column-gap: 50px;
    padding: 50px 100px;
    background: lighten($gray, 50%);
    form {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        input[type="email"] {
            width: 75%;
            height: 3em;
            background: white;
            border: none;
            border-radius: 0;
            color: $dark;
            padding-left: 1em;
            padding-right: 1em; }
        input[type="submit"] {
            height: 3em;
            background: $bright;
            @include tablet {
                width: 25%; }
            color: white;
            border: none;
            border-radius: 0; } } }
section.signup-4 {
    padding: 50px;
    @include tablet {
        padding: 75px 100px; }
    grid-template-columns: repeat(12, 1fr);
    display: grid;
    form {
        background-color: white;
        border-radius: 3px;
        grid-column: 1 / -1;
        @include tablet {
            grid-column: 5 / -1; }
        @include desktop {
            grid-column: 8 / -1; }
        text-align: center;
        padding: 60px;
        input[type="text"], input[type="email"] {
            background: lighten($gray, 50%);
            color: $dark;
            border: none;
            margin-bottom: 1em;
            padding: 1em; }
        input[type="submit"] {
            padding: 1em 2em;
            color: white;
            background-color: $bright;
            width: 100%; } } }



